import * as React from 'react';
import { useParams, useSearchParams } from 'react-router-dom';

import type { TSDState } from '@miq/contextjs';
import type { TProduct } from '../../types';

import Staff from '@miq/staffjs';
import { SharedDataCtx } from '@miq/contextjs';
import { Loading, Pagination, Viewports } from '@miq/componentjs';

import { ProductCardList } from '../components';

const ProductUpdateView = React.lazy(() => import('./views.Update'));

export default function ProductUpdateRoute() {
  const { prodSlug } = useParams();
  const [params] = useSearchParams();
  const { reload, product } = React.useContext<TSDState<{ product: TProduct }>>(SharedDataCtx);

  React.useEffect(() => {
    reload({ params });
  }, [params, prodSlug]);

  return (
    <div className="p-update-route d-grid grid-lg-4 gap-2">
      <div className="col main span-lg-3">
        {prodSlug !== product?.slug ? <Loading icon /> : <ProductUpdateView back="/staff/store/products/" />}
      </div>

      <Viewports.LGUp>
        <AsideView />
      </Viewports.LGUp>
    </div>
  );
}

const AsideView = () => {
  const { products, pagination } = React.useContext<{ products: TProduct[]; pagination: any }>(SharedDataCtx);

  return (
    <div className="col aside pe-2">
      <div className="mb-1 mt-2">{products && <ProductCardList items={products} itempath="../" />}</div>

      <div className="pb-4" style={{ position: 'sticky', bottom: 3, overflowX: 'auto' }}>
        <Pagination {...pagination} component={Staff.Link} to />
      </div>
    </div>
  );
};
